
/* PROJECTS STYLE */

.projMainBox {
    height: auto;
    padding: 1%;
    padding-top: 6%;
    background-color: #35364a;
}

.myProjectStyle{
    font-size: 300%;
    padding: 2%;
    font-weight: bold;
    text-shadow: 1px 2px #000000;
    color: #f49030;
}

.card-img-top{
    object-fit: cover;
}

.card-title{
    font-family: 'Roboto', sans-serif;
    color: black;
    font-size: 30px;
    font-weight: 900;
}

.cardObjective{
    color: black;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
}

.sentence{
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 400;
}

.card-body{
    background-color: #f49030;
    color: black;
}

.card{
    border: 1px solid black;
    box-shadow: 2px 5px 5px 0 rgb(0, 0, 0), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    height: 100%;
}

.buttonProject{
    background-color: #3c3f4c;
    border: 2px solid white;
    font-size: 18px;
    font-weight: 500;
    color: #f49030;
}

.buttonProject:hover{
    background-color: white;
    border: 2px solid black;
    transition: 1.5s;
    color: #35364a;
}
/* Media Query */
@media (max-width: 480px) {
    .myProjectStyle{
        font-size: 350%;
        padding: 3%;
        margin-top: 20%;
        font-weight: bold;
        text-shadow: 1px 2px #000000;
        color: #f49030;
    }
    .card{
        margin: 1%;
    }
    .card-title{
        font-family: 'Roboto', sans-serif;
        color: black;
        font-size: 25px;
        font-weight: 900;
    }

    .cardObjective{
        color: black;
        font-family: 'Roboto', sans-serif;
        font-size: 18px;
    }

    .sentence{
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
        font-weight: 400;
    }
}