
/* Skills Page Styles */
.my-services {
    height: auto;
    padding: 1%;
    padding-top: 6%;
    background-color: #35364a;
    text-shadow: 1px 2px #000000;
}

/* All Icon Styles */
.my-service-item i{
    width: 100%;
    text-align: center;
}

/* H3 style */
.my-services h3 {
    font-family: 'Roboto', sans-serif;
    margin-bottom: 20px;
    font-size: 210%;
    font-weight: 650;
    letter-spacing: 2px;
    text-align: center;
    line-height: 50px;
    text-shadow: 2px 0 1px #fff0f0fa;
}

/* Skills Icons Style*/
.fa-html5 {
    background-color: #ef863d;
    border-radius: 5px;
    padding: 10%;
    color: #5d6172;
    margin-bottom: 20px;
    box-shadow: rgb(0, 0, 0) 8px 8px 3px;
}

