/* ABOUT PAGE STYLES */
.aboutmainbox {
    padding: 1%;
    padding-top: 6%;
    height: auto;
    background-color: #35364a;
}

.myStoryStyle {
    font-size: 350%;
    padding: 2%;
    font-weight: bold;
    text-shadow: 1px 2px #000000;
    color: #f49030;
}

.carousel-item img {
    width: 45%;
    height: auto;
    margin: auto;
    border: 3px solid #f49030;
    border-radius: 4px;
}

.aboutDetails {
    font-size: 28px;
    margin: 2%;
    padding: 2%;
    border-radius: 15px;
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-shadow: 2px 2px #ffffff34;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
    background-color: #f49030;
    color: black;
}

.pill {
    border-radius: 4px;
    margin-bottom: 5%;
    color: #f49030;
    border: 4px solid currentColor;
    width: 100%;
}

/* Media Query */
@media (max-width: 480px) {
    .myStoryStyle {
        margin-top: 20%;
    }

    .aboutDetails {
        font-size: 16px;
        margin: 5%;
        padding: 2%;
        border-radius: 15px;
        font-family: 'Roboto', sans-serif;
        text-align: justify;
        text-shadow: 2px 1px #ffffff34;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
        background-color: #f49030;
        color: black;
    }

}