/* EXPERIENCE STYLES */
.my-experience {
    height: auto;
    padding: 1%;
    padding-top: 6%;
    background-color: #35364a;
}

.myExperienceStyle{
    font-size: 350%;
    padding: 2%;
    font-weight: bold;
    text-shadow: 1px 2px #000000;
    color: #f49030;
}

.my-education h3 {
    font-size: 60px;
    line-height: 50px;
    font-weight: 400;
    margin-bottom: 60px;
    font-family: 'Roboto', sans-serif;
    text-shadow: 2px 3px 0px rgba(255, 255, 255, 0.334);
    color: 	#f49030;
}

.my-education .my-education-details .my-education-item {
    margin-bottom: 30px;
    padding: 24px 30px;
    border-radius: 4px;
    border: 2px solid white;
    box-shadow: 0 2px 20px 0 rgba(255, 255, 255, 0.334), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.my-education .my-education-details .my-education-item:last-child {
    margin-bottom: 0;
}

.my-education-item h4 {
    font-size: 25px;
    line-height: 30px;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.my-education-item .my-eduyear {
    margin: 10px 0;
    color: #0bceaf;
    font-size: 22px;
    font-family: 'Roboto', sans-serif;
}

.my-education {
    padding: 30px;
}


/* Work Styles */
.my-work h3 {
    font-size: 350%;
    line-height: 50px;
    font-weight: 400;
    margin-bottom: 60px;
    font-family: 'Roboto', sans-serif;
    text-shadow: 2px 3px 0px rgba(255, 255, 255, 0.334);
    color: #f49030;
}

.my-work .my-work-details .my-work-item {
    margin-bottom: 30px;
    padding: 25px 30px;
    border-radius: 4px;
    border: 2px solid white;
    box-shadow: 0 3px 20px 0 rgba(255, 255, 255, 0.334), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.my-work .my-work-details .my-work-item:last-child {
    margin-bottom: 0;
}

.my-work-item h4 {
    font-size: 25px;
    line-height: 30px;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.my-work-item .my-workyear {
    margin: 10px 0;
    color: #0bceaf;
    font-size: 22px;
    font-family: 'Roboto', sans-serif;
}

.work-responsibility li {
    font-size: 17px;
    line-height: 25px;
    margin: 5px 0;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.my-work {
    padding: 30px;
}

/* Photography Style */
.my-albums {
    height: auto;
    padding: 1%;
    background-color: #1e1e1e;
}

/* Media Query */
@media (max-width: 480px) {
    .myExperienceStyle{
        padding: 25% 3%;
        font-size: 300%;
    }

    .my-education{
        padding: 3%;
    }

    .my-work{
        padding: 3%;
    }

    .my-education h3 {
        font-size: 60px;
        line-height: 50px;
        font-weight: 400;
        margin-bottom: 30px;
        font-family: 'Roboto', sans-serif;
        text-shadow: 2px 3px 0px rgba(255, 255, 255, 0.334);
        color: 	#f49030;
    }

    .my-work h3{
        font-size: 350%;
        line-height: 50px;
        font-weight: 400;
        margin-bottom: 30px;
        font-family: 'Roboto', sans-serif;
        text-shadow: 2px 3px 0px rgba(255, 255, 255, 0.334);
        color: #f49030;
    }
}