
/* CONTACT STYLE */
.contact {
    height: auto;
    padding: 1%;
    padding-top: 6%;
    padding-bottom: 5%;
    background-color: #35364a;
}

.contactInfo .box {
    align-content: center;
    display: flex;
    padding: 6%;
}

.contactInfo .box .icon{
    height: 80px;
    min-width: 70px;
    background-color: #ef863d;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 40px;
    color: #fff;
}

.contactInfo .box .text{
    display: flex;
    margin-left: 15px;
    font-size: 17px;
    color: #fff;
    flex-direction: column;
    font-weight: 500;
}

.contactInfo .box .text h3{
    font-weight: 600;
    color: #ef863d;
}

/* Forms Styles */
.myContactMe {
    font-size: 350%;
    padding: 2%;
    font-weight: bold;
    text-shadow: 1px 2px #000000;
    color: #f49030;
}

.contactForm{
    color: white;
    padding: 25px 20px; 
    border-radius: 4px;
    border: 2px solid white;
    box-shadow: 0 3px 20px 0 rgba(255, 255, 255, 0.334), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.formName{
    font-size: 25px;
    padding: 2px;
}

/* Button Style */
.btnSubmitCss{
    background-color: #3c3f4c;
    border: 2px solid white;
    font-size: 25px;
    font-weight: 500;
    color: #f49030;
}

.btnSubmitCss:hover{
    background-color: #f49030;
    transition: 1.5s;
    color:black;
}

