/* Media Query */
@media (max-width: 480px) {
    .myContactMe {
        margin-top: 20%;
    }

    .textStyle{
        font-size: 100%;
    }

    i.iconStyle{
        background-color: #ef863d;
        padding: 3%;
        border-radius: 50%;
    }

    .iconText{
        font-weight: bold;
        padding: 1%;
    }

    .btnSubmitCss{
        background-color: #3c3f4c;
        border: 2px solid white;
        font-size: 20px;
        font-weight: 500;
        color: #f49030;
    }
}


