/* Hero Page Style */
.heroPage {
    height: auto;
    background: rgb(239,134,61);
    background: linear-gradient(180deg, rgba(239,134,61,1) 75%, rgba(53,54,74,1) 98%);
    padding-bottom: 3%;
}

/* details style == Heropage */
.details {
    margin-top: 5%;
    -webkit-text-stroke: 0px #282828;
    text-shadow: 1px 0px 0px rgb(22, 22, 22);
    font-family: 'Roboto', sans-serif;
}

.detailsH1 {
    color: #35364a;
    font-size: 365%;
    -webkit-text-stroke: 0px hsl(0, 0%, 16%);
    text-shadow: 1px 1px 0px rgb(22, 22, 22);
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}

.detailH1 {
    font-size: 250%;
    font-weight: 150;
    color: #35364a;
}

.detailH3 {
    font-size: 200%;
    font-weight: 50;
    color: #35364a;
}

.detailH5 {
    font-size: 160%;
    font-weight: 20;
    color: #35364a;
}

/* Profile Picture style */
.profilePicture {
    border-radius: 50%;
    -webkit-box-shadow: 0px -0px 10px 0px rgba(0, 0, 0, 0.79);
    -moz-box-shadow: 0px -0px 10px 0px rgba(0, 0, 0, 0.79);
    box-shadow: 0px -0px 10px 0px rgba(0, 0, 0, 0.79);
}

.profileRow {
    padding: 4%;
}

.profileBox {
    margin-bottom: 10%;
}

/* Button Style Hero Page */
.buttonCss {
    -webkit-box-shadow: 3px 2px 0px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 3px 2px 0px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 3px 2px 0px 0px rgba(0, 0, 0, 0.75);
    padding: 1% 2%;
    margin: 1%;
    background-color: #3c3f4c;
    border: 2px solid white;
    font-size: 30px;
    font-weight: 600;
    color: #f49030;
}

.buttonCss:hover {
    box-shadow: 0 0 40px 40px rgba(0, 0, 0, 0.533) inset;
    color: #ef863d !important;
}


/* MEDIA QUERY */
@media (max-width: 480px) {
    .heroPage {
        height: auto;
        background: rgb(239,134,61);
        background: linear-gradient(180deg, rgba(239,134,61,1) 75%, rgba(53,54,74,1) 98%);
        padding-top: 10%;
        padding-bottom: 50%;
    }

    .profileBox {
        padding: 2%;
    }

    .detailsH1 {
        color: #35364a;
        font-size: 250%;
        -webkit-text-stroke: 0px hsl(0, 0%, 16%);
        text-shadow: 1px 1px 0px rgb(22, 22, 22);
        font-family: 'Roboto', sans-serif;
        font-weight: bold;
    }

    .detailH1 {
        font-size: 170%;
        font-weight: 500;
        color: #35364a;
    }

    .detailH3 {
        font-size: 150%;
        font-weight: 50;
        color: #35364a;
    }


    .detailH5 {
        font-size: 125%;
        font-weight: 20;
        color: #35364a;
    }
}

@media (max-width: 768px) {
    .profileRow {
        padding: 5%;
    }
    .profileBox {
        margin-bottom: 25%;
    }
}